
import "./play.scss"

export class Play extends Component{
    constructor(){
        super()
        this.state={
            flag:true
        }
    }
    componentDidMount () {    
        var map = new BMap.Map("mapContainer"); // Map
        map.centerAndZoom(new BMap.Point(116.404, 39.915), 11); // ,
        map.addControl(new BMap.MapTypeControl()); //
        map.setCurrentCity(""); //  
        map.enableScrollWheelZoom(true); //
        var geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == BMAP_STATUS_SUCCESS){
                var mk = new BMap.Marker(r.point);
                map.addOverlay(mk);
                map.panTo(r.point);
                // alert(''+r.point.lng+','+r.point.lat);
            }
            else {
                alert('failed'+this.getStatus());
            }        
        },{enableHighAccuracy: true})
    }
    gostart=()=>{
        if(this.state.flag){
            this.refs.begin.innerHTML=""
            this.refs.begin.style.backgroundColor="red"
            this.setState({
                flag:false
            })
        }else{
            this.refs.begin.innerHTML=""
            this.refs.begin.style.backgroundColor="#189adb"
            this.setState({
                flag:true
            })
        }
        
    }
    render(){
        return(
            <div className="p-box">
                <div id="mapContainer" style={{width:"100%",height:200,marginTop:15}}></div>
                <div className="p-number">
                    <ol>
                        <li>
                            <p>00.00</p>
                            <span>(KM)</span>
                        </li>
                        <li>
                            <p>00:00</p>
                            <span></span>
                        </li>
                        <li>
                            <p>00.00</p>
                            <span>(KM/h)</span>
                        </li>
                    </ol>
                </div>
                <div className="p-click" onClick={this.gostart} ref="begin">
                    
                </div>
            </div>
        )
    }
}


